.header {
  padding: 20px 0;
  position: relative;
  z-index: 5;
  background-color: #fff;
}

@media (max-width: 991px) {
  .header {
    padding: 25px 0;
  }
}

.header__row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}

.header__logo {
  display: flex;
  align-items: center;
  position: relative;
  z-index: 1;
}

@media (max-width: 991px) {
  .header__logo {
    justify-content: center;
    margin: 0 auto;
  }
}

.header__logo:hover {
  text-decoration: none;
}

.header__logo img,
.header__logo svg {
  max-width: 110px;
}

.header__logo span {
  font-family: "Lexend", sans-serif;
  font-weight: 300;
  font-size: 19px;
  margin: 0 0 0 10px;
  color: #E5294B;
}

@media (max-width: 479px) {
  .header__logo span {
    display: none;
  }
}

@media (max-width: 991px) {
  .header__nav {
    position: fixed;
    overflow: auto;
    z-index: 1;
    left: 0;
    right: 0;
    top: 0px;
    background: #fff url("../images/header/menu-preview@1x.jpg") right 50px no-repeat;
    padding: 40px 0 50px 0;
    transform: translateY(-110%);
    opacity: 0;
  }
}

@media (max-height: 600px) and (max-width: 991px) {
  .header__nav {
    bottom: 80px;
  }
}

@media (max-width: 767px) {
  .header__nav {
    padding: 30px 0 40px 0;
    background-position: right 40px;
    background-size: 80%;
  }
}

@media (max-width: 479px) {
  .header__nav {
    background-image: none;
  }
}

.header__nav-list {
  display: flex;
  align-items: center;
}

@media (max-width: 991px) {
  .header__nav-list {
    flex-direction: column;
    align-items: flex-start;
    margin: 0 0 70px 0;
  }
}

@media (max-width: 479px) {
  .header__nav-list {
    margin: 0 0 50px 0;
  }
}

@media (max-width: 479px) {
  .header__nav-list > li {
    text-align: center;
    width: 100%;
  }
}

.header__nav-list > li:not(:last-child) {
  margin: 0 60px 0 0;
}

@media (max-width: 1199px) {
  .header__nav-list > li:not(:last-child) {
    margin: 0 40px 0 0;
  }
}

@media (max-width: 991px) {
  .header__nav-list > li:not(:last-child) {
    margin: 0 0px 40px 0;
  }
}

.header__nav-list > li > a:not(.btn) {
  color: #212529;
  font-weight: 400;
  font-size: 16px;
}

.header__nav-list > li > a:not(.btn):hover {
  color: #E5294B;
  text-decoration: none;
}

@media (max-width: 991px) {
  .header__nav-list > li > a:not(.btn):hover {
    font-weight: 600;
  }
}

@media (max-width: 991px) {
  .header__nav-list > li > a.btn {
    border-color: #E5294B;
    color: #E5294B;
  }
}

@media (max-width: 991px) {
  .header__nav-list > li > a.btn:hover {
    color: #fff;
  }
}

.header__nav-list > li.visible-md {
  display: none;
}

@media (max-width: 991px) {
  .header__nav-row {
    max-width: 690px;
    margin: 0 auto;
    padding-left: 15px;
    padding-right: 15px;
    display: flex;
    flex-wrap: wrap;
  }
}

@media (max-width: 991px) {
  .header__nav-row-left {
    width: 180px;
  }
}

@media (max-width: 479px) {
  .header__nav-row-left {
    width: 100%;
    text-align: center;
  }
}

.header__nav-row-right {
  display: none;
}

@media (max-width: 991px) {
  .header__nav-row-right {
    display: flex;
    width: calc(100% - 180px);
    align-items: flex-start;
    justify-content: flex-end;
  }
}

@media (max-width: 479px) {
  .header__nav-row-right {
    display: none;
  }
}

.header__nav-row-right img {
  object-fit: cover;
  width: 100%;
}

.header__nav-phone,
.header__nav-email {
  display: none;
  font-weight: 500;
  font-size: 14px;
  font-family: "Montserrat", sans-serif;
  text-decoration: none;
  margin: 0 0 30px 0;
}

@media (max-width: 991px) {
  .header__nav-phone,
  .header__nav-email {
    display: flex;
  }
}

@media (max-width: 479px) {
  .header__nav-phone,
  .header__nav-email {
    text-align: center;
    justify-content: center;
  }
}

.header__nav-phone a:hover,
.header__nav-email a:hover {
  color: #E5294B;
  text-decoration: none;
}

.header__nav-socials {
  display: none;
}

@media (max-width: 991px) {
  .header__nav-socials {
    display: flex;
  }
}

@media (max-width: 479px) {
  .header__nav-socials {
    justify-content: center;
    align-items: center;
    width: 100%;
  }
}

.header__nav-socials > li:not(:last-child) {
  margin: 0 32px 0 0;
}

.header__nav-socials > li a {
  display: flex;
  align-items: center;
  position: relative;
}

.header__nav-socials > li a:hover::after {
  bottom: -12px;
  opacity: 1;
}

.header__nav-socials > li a:hover svg path {
  fill: #E5294B;
}

.header__nav-socials > li a svg {
  height: 24px;
}

.header__nav-socials > li a svg path {
  transition: all 0.18s linear;
}

.header__nav-socials > li a::after {
  content: "";
  display: block;
  position: absolute;
  width: 4px;
  height: 4px;
  border-radius: 50%;
  background-color: #E5294B;
  left: 50%;
  transform: translateX(-50%);
  transition: all 0.18s linear;
  opacity: 0;
  bottom: -16px;
}

.header__burger {
  width: 40px;
  height: 40px;
  outline: none !important;
  display: none;
  border: none;
  background: none;
  margin: 2px 0 0 0;
  padding: 0;
  position: absolute;
  z-index: 3;
  left: -8px;
  top: 50%;
  transform: translateY(-50%);
  cursor: pointer;
}

@media (max-width: 991px) {
  .header__burger {
    display: block;
  }
}

.header__burger span {
  position: absolute;
  display: block;
  height: 2px;
  background-color: #E5294B;
  width: 15px;
  border-radius: 6px;
  left: 10px;
  transition: all 0.18s linear;
}

.header__burger span:nth-child(1) {
  top: 13px;
}

.header__burger span:nth-child(2) {
  top: 50%;
  margin-top: -1px;
  width: 18px;
}

.header__burger span:nth-child(3) {
  bottom: 13px;
}

.menu_active {
  overflow: hidden;
  position: relative;
}

.menu_active::after {
  content: "";
  display: block;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 2;
  background-color: rgba(0, 0, 0, 0.3);
}

.menu_active .header::after {
  content: "";
  display: block;
  position: absolute;
  z-index: 1;
  left: 0;
  right: 0;
  bottom: 0px;
  background-color: transparent;
  box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.07);
  height: 20px;
}

.menu_active .header__burger span:nth-child(1) {
  transform: rotate(-45deg);
  top: 19px;
}

.menu_active .header__burger span:nth-child(2) {
  width: 0;
}

.menu_active .header__burger span:nth-child(3) {
  transform: rotate(45deg);
  bottom: 19px;
}

.menu_active .header__nav {
  transform: translateY(78px);
  opacity: 1;
}

